import React from 'react'
import {ButtonGroup, Button} from 'reactstrap'
import {useDispatch, useSelector} from "react-redux";
import {changeIsGuest} from "../../redux/actions/auth";
import {FormattedMessage} from "react-intl";

const AppSelection = () => {

  const {isGuest} = useSelector(state => state.auth)
  const dispatch = useDispatch()

  const onClickChangeAppType = (value) => {
    dispatch(changeIsGuest(value))
  }

  return (
    <div className='text-center mb-2'>
      <ButtonGroup>
        <Button className='btn-app-selection' active={!isGuest} type='button'
                onClick={() => onClickChangeAppType(false)}>
          <FormattedMessage id='socialLogin.agent'/>
        </Button>
        <Button className='btn-app-selection' active={isGuest} onClick={() => onClickChangeAppType(true)} type='button'>
          <FormattedMessage id='socialLogin.personal'/>
        </Button>
      </ButtonGroup>
    </div>
  )
}

export default AppSelection
